<template>
  <el-row>
    <a @click="goUp()"><img :src="goUpSrc" alt=""></a>
  </el-row>
</template>
<script>
  export default {
    name: "goTop",
    data() {
      return {
      goUpSrc:"./img/icons/up.svg"
      }
    },
    methods: {
      goUp: function () {
        let currentPosition = document.documentElement.scrollHeight || document.body.scrollTop
        let s = setInterval(function() {
          if(currentPosition>1) {
            currentPosition -= currentPosition/4;
            window.scrollTo(0, currentPosition);
          } else {
            clearInterval(s)
          }
        },50)
      }
    }
  }
</script>

